import React, { Component } from 'react';

class Nav extends Component {
  state = {
    navItems: ['Factory', 'Recycle', 'Distribution','Users'],
  };

  render() {
    return ( 
      <nav className='navbar-expand-md navbar-light bg-white '>
        <ul className='navbar-nav justify-content-end'>
          {this.state.navItems.map((item) => (
            <li className='nav-item px-2' key={item.length}>
              <a className='nav-link ' href='#'>
                <div className='col-xs-1 text-center'>
                  <img src={'%PUBLIC_URL%/image/' + item + '.png'} width='30' height='30' alt='' />
                  <p>{item}</p>
                </div>
              </a>
            </li>
          ))}
        </ul>
      </nav>
    );
  }
}

export default Nav;
